<script>
    export let isOpened = false;
    export let updateParent = () => {};

    import ModalDialog from "./ModalDialog.svelte";
    import { getRequiredFields, createCustomTable } from "../js/requests.js";

    let columns = [];
    getRequiredFields().then(res => {
        columns = res.map(el => ({ value: el, required: true }));
    });

    function handleCreateNewRow() {
        columns.push({ value: "", required: false });
        columns = columns;
    }

    function handleSubmit() {
        const name = prompt("Enter table name");
        if (name != null && name != "") {
            let keys = columns.map(col => col.value);
            createCustomTable({ Name: name, keys }).then(() => {
                updateParent();
                isOpened = false;
            });
        }
    }
</script>

<style>
    ul {
        list-style: none;
        margin-top: 0;
    }
    .submit {
        background-image: linear-gradient(
            rgba(52, 206, 87, 1),
            rgba(40, 167, 69, 1)
        );
        border: 1px solid #258c3e;
        color: white;
        float: right;
    }

    .submit:hover {
        background-image: linear-gradient(
            rgba(52, 206, 87, 1),
            rgba(52, 206, 87, 1)
        );
    }
    .footer {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
</style>

<ModalDialog bind:isOpened width="300px">
    <h2 slot="header">Create Table</h2>
    <ul slot="body">
        {#each columns as column}
            <li>
                <input
                    placeholder="New row"
                    disabled={column.required}
                    bind:value={column.value} />
            </li>
        {/each}
    </ul>
    <div class="footer" slot="footer">
        <div class="footer-left">
            <button on:click={handleCreateNewRow} class="submit">
                New row
            </button>
        </div>
        <div class="footer-right">
            <button on:click={handleSubmit} class="submit">Submit</button>
        </div>
    </div>
</ModalDialog>
