<script>
    export let isOpened = false;
    export let isAdd = false;
    export let withPositionPicker = false;
    export let keys = [];
    export let values = [];

    import { createEventDispatcher } from "svelte";
    import Map from "./Map.svelte";

    import cloneDeep from "lodash.clonedeep";

    const dispatch = createEventDispatcher();
    let newValues;
    function setNewValues(values) {
        newValues =
            Array.isArray(values) && values.length === keys.length
                ? cloneDeep(values)
                : Array(keys.length).fill("");
    }
    $: setNewValues(values);
    $: display = isOpened ? "block" : "none";
    let displayMap = false;

    let getCors;

    function handleMapOpen() {
        if (displayMap) {
            displayMap = false;
        } else {
            displayMap = true;
        }
    }

    function handleMapPick({ lat, lng }) {
        newValues[keys.indexOf("Latitude")] = lat;
        newValues[keys.indexOf("Longitude")] = lng;
        newValues = [...newValues];
    }

    function handleDialogClose() {
        dispatch("close", {});
    }

    function handleSave() {
        dispatch("close", {});
        if (isAdd) {
            dispatch("add", { row: newValues });
        } else {
            dispatch("save", { row: newValues });
        }
    }
</script>

<style>
    header {
        padding: 15px;
        display: flex;
        justify-content: space-between;
    }

    h2 {
        margin-top: 0;
    }

    .modal {
        display: none; /* Hidden by default */
        position: fixed; /* Stay in place */
        z-index: 1000; /* Sit on top */
        left: 0;
        top: 0;
        width: 100%; /* Full width */
        height: 100%; /* Full height */
        overflow: auto; /* Enable scroll if needed */
        background-color: rgb(0, 0, 0); /* Fallback color */
        background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
    }

    /* Modal Content/Box */
    .modal-content {
        transition: 1s;
        background-color: #fefefe;
        margin: 7% auto; /* 15% from the top and centered */
        border: 1px solid #888;
        border-radius: 4px;
        width: 80%; /* Could be more or less, depending on screen size */
    }

    /* The Close Button */
    .close {
        height: fit-content;
        line-height: 1;
        padding: 8px 16px;
        color: #aaa;
        font-size: 28px;
        font-weight: bold;
        float: right;
    }

    .close:hover,
    .close:focus {
        color: black;
        text-decoration: none;
        cursor: pointer;
    }

    .modal-body {
        margin: 0 auto;
        max-width: 90%;
        overflow-x: scroll;
    }
    table,
    td,
    th {
        border: 1px solid #ddd;
        text-align: left;
    }

    table {
        border-collapse: collapse;
        width: 100%;
    }
    th {
        background-color: #1a73e8;
        color: white;
    }

    th,
    td {
        width: auto;
        min-height: 40px;
        min-width: 40px;
        padding: 7px;
        white-space: nowrap;
    }

    footer {
        height: 37px;
        margin: 20px 0;
        padding: 0 20px;
        background-color: white;
        transition: 1s;
    }

    .map-hidden {
        display: none;
    }

    .map {
        width: 100%;
        height: 500px;
    }

    button {
        width: 100px;
        white-space: nowrap;
        border-radius: 4px;
    }

    .submit {
        background-image: linear-gradient(
            rgba(52, 206, 87, 1),
            rgba(40, 167, 69, 1)
        );
        border: 1px solid #258c3e;
        color: white;
        float: right;
    }

    .submit:hover {
        background-image: linear-gradient(
            rgba(52, 206, 87, 1),
            rgba(52, 206, 87, 1)
        );
    }

    .primary {
        border: none;
        margin: 0 5px;
        /* background-image: linear-gradient(rgba(65,165,238,1), rgba(26,115,132,1)); */
        background-color: #1867c0;
        color: white;
        float: right;
    }

    .primary:hover {
        background-color: #2a73c5;
    }

    button:hover {
        cursor: pointer;
    }
</style>

<div id="modal" style={`display:${display}`} class="modal">
    <!-- Modal content -->
    <div class="modal-content">
        <div class="close" on:click={handleDialogClose}>&times;</div>
        <header>
            <div>
                <h2>{isAdd ? 'Add' : 'Edit'} Dialog</h2>
            </div>
        </header>
        <div class="modal-body">
            <table>
                <tr>
                    {#each keys as key}
                        <th>{key}</th>
                    {/each}
                </tr>
                <tr>
                    {#each newValues as value, i}
                        <td>
                            <input
                                disabled={i === 0 && !isAdd}
                                type="text"
                                bind:value />
                        </td>
                    {/each}
                </tr>
            </table>
        </div>

        {#if withPositionPicker}
            <div class="map" class:map-hidden={!displayMap}>
                <Map
                    on:mount={event => {
                        getCors = event.detail.getCors;
                    }}
                    on:returnPosition={handleMapPick} />
            </div>
        {/if}
        <footer>
            {#if displayMap}
                <button
                    style="float:left;width:150px"
                    on:click={() => {
                        handleMapPick(getCors());
                    }}>
                    Get Coordinates
                </button>
            {/if}
            <button class="submit" on:click={handleSave}>Save</button>
            {#if withPositionPicker}
                <button class="primary" on:click={handleMapOpen}>
                    {displayMap ? 'Hide Map' : 'Use Map'}
                </button>
            {/if}
        </footer>
    </div>

</div>
