<script>
    import { onMount } from "svelte";
    import { createEventDispatcher } from "svelte";
    import LocationPicker from "location-picker";
    import { isMapReady } from "../stores.js";

    let container;
    let picker;
    let zoom = 8;
    let center = { lat: -34.397, lng: 150.644 };

    const dispatch = createEventDispatcher();

    let isMapReadyValue;
    let isMounted = false;

    const unsubscribe = isMapReady.subscribe(v => (isMapReadyValue = v));

    const getCors = () => picker.getMarkerPosition();

    onMount(async () => {
        isMounted = true;
        dispatch("mount", { getCors });
    });
    $: {
        if (isMapReadyValue && isMounted) {
            console.log("map is ready");
            picker = new LocationPicker(
                container,
                {
                    lat: 50.519260657576844,
                    lng: 30.57048504198045,
                    setCurrentPosition: true
                },
                {
                    zoom: 15 // You can set any google map options here, zoom defaults to 15
                }
            );
        }
    }
</script>

<style>
    .hidden {
        display: none;
    }
    .map {
        transition: 1s linear;
        height: 100%;
        width: 100%;
    }
</style>

<div bind:this={container} class:hidden={!isMapReadyValue} class="map" />
